import { useState, useEffect } from 'react';

export default function ThemeSwitcher() {
    const [isDark, setIsDark] = useState<boolean | null>(null);

    useEffect(() => {
        const savedTheme = localStorage.getItem('theme');

        if (savedTheme) {
            setIsDark(savedTheme === 'dark');
            document.documentElement.classList.toggle('dark', savedTheme === 'dark');
            document.documentElement.setAttribute('data-theme', 'true'); // Добавляем атрибут data-theme
        } else {
            const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
            setIsDark(prefersDark);
            document.documentElement.classList.toggle('dark', prefersDark);
            document.documentElement.setAttribute('data-theme', 'true'); // Добавляем атрибут data-theme
        }
    }, []);

    const toggleTheme = () => {
        const newTheme = isDark ? 'light' : 'dark';
        setIsDark(!isDark);
        localStorage.setItem('theme', newTheme);
        document.documentElement.classList.toggle('dark', !isDark);
    };

    if (isDark === null) {
        return null;
    }

    return (
        <button onClick={toggleTheme}  className="p-2 px-4 rounded-md bg-white text-black dark:bg-gray-800 dark:text-white shadow-md hover:shadow-lg transition-all">
            {isDark ? 'Светлая тема' : 'Темная тема'}
        </button>
    );
}
